import React, { Suspense } from "react";
import { QueryClient, QueryClientProvider } from "react-query";
import queryString from "query-string";

import { Choose, Otherwise, When } from "./jk/types/jsxControlStatements";

const LazyCapabilitiesInitializerPage = React.lazy(() => import("./pages/CapabilitiesInitializerPage"));
const LazyIndexPage = React.lazy(() => import("./pages/IndexPage"));
const LazyDummyPage = React.lazy(() => import("./pages/DummyPage"));
const LazyBoardSummaryPage = React.lazy(() => import("./pages/BoardSummaryPage"));
const LazyCardBackSectionPage = React.lazy(() => import("./pages/CardBackSectionPage"));

const queryClient = new QueryClient();

const searchParams = queryString.parse(location.search) as RouteSearchParams;
const path = searchParams.path;

// console.log("href", window.location.href);
// console.log("search", searchParams);

function App() {
    return (
        <QueryClientProvider client={queryClient}>
            <Suspense fallback="loading...">
                <Choose>
                    <When condition={path === "/" || path === undefined}>
                        <LazyCapabilitiesInitializerPage />
                    </When>
                    <When condition={path === "/dummy"}>
                        <LazyDummyPage />
                    </When>
                    <When condition={path === "/board-page"}>
                        <LazyBoardSummaryPage />
                    </When>
                    <When condition={path === "/card-back-section"}>
                        <LazyCardBackSectionPage />
                    </When>
                    <Otherwise>
                        <LazyIndexPage />
                    </Otherwise>
                </Choose>
            </Suspense>
        </QueryClientProvider>
    );
}

export default App;
